import { Button, Container, Divider, FormControl, FormControlLabel, FormLabel, Link, List, ListItem, ListItemText, Radio, RadioGroup, Slider, TextField, Typography } from '@mui/material'
import React, { useRef, useState } from 'react'

interface Nimi{
    id : number;
    Nimi : string;
    Lukumaara : number;
  }

interface Data {
    nimitiedot : any[],
    sukunimi: any[],
    virhe : string,
    dataHaettu : boolean
  }

const Nimitietoa : React.FC<any> = (props : any) : React.ReactElement => {

    const formRef : any = useRef<HTMLFormElement>()
    const [data, setData] = useState<Data>({
        nimitiedot : [],
        sukunimi : [],
        virhe : "",
        dataHaettu : false
      });

      const haeNimitiedot = async (e : React.FormEvent) : Promise<void> => {

          e.preventDefault();

          try {
            const [etunimet, sukunimet] = await Promise.all([
              fetch(`/api/${formRef.current.sukupuoli.value}/${formRef.current.etunimi.value}`),
              fetch(`/api/sukunimi/${formRef.current.sukunimi.value || null}`)
            ]);
            
            setData({
                ...data,
                nimitiedot : await etunimet.json(),
                sukunimi : await sukunimet.json(),
                dataHaettu : true
            });
              
          } catch (error) {
              console.log(error);
          }
      };

    //   const nimiPaiva : any = async (nimi) : Promise<void> => {
    //       const yhteys = await fetch("https://nameday.abalin.net/api/V1/getname", {
    //           method: "POST",
    //           headers: {
    //             "Content-Type": "application/json",
    //             "Accept": "application/json",
    //           },
    //           body: JSON.stringify({
    //               "name" : nimi,
    //               "country" : "fi"
    //           })
    //       })

    //       setData({
    //           ...data,
    //           nimipaiva: yhteys.json()
    //       })
    //   };
      
  return (
<>
        <Typography variant="h3" sx={{textAlign:"center", fontWeight:"bold"}}>Nimitietoa</Typography>

        <Container
        ref={formRef}
        component="form"
        onSubmit={haeNimitiedot}
        sx={{marginTop:4, marginBottom:4}}
        >
          <FormControl fullWidth>
          <FormLabel>Etunimet</FormLabel>
            <TextField fullWidth variant="outlined" name="etunimi"/>
          <FormLabel>Sukunimi</FormLabel>
          <TextField fullWidth variant="outlined" name="sukunimi"/>
          <FormLabel>Sukupuoli</FormLabel>
            <RadioGroup
              defaultValue="miehet"
              row
              name="sukupuoli"
            >
              <FormControlLabel value="miehet" control={<Radio />} label="Mies" />
              <FormControlLabel value="naiset" control={<Radio />} label="Nainen" />
            </RadioGroup>

        <Button
        sx={{width:"40%", margin:"auto"}}
        type="submit"
        variant="contained"
          >
              Hae nimitietoja
          </Button>
          </FormControl>
        </Container>

          <Container sx={{textAlign:"center"}}>
              {(data.dataHaettu !== false)
              ?<>
              <Divider/>
              <List>
                  <ListItem>
                      <ListItemText
                      secondary={
                        (formRef.current.sukupuoli.value === "miehet")
                        ?<React.Fragment>
                            <Link
                            component="button"
                            onClick={() => {window.open(`https://lapsennimi.com/nimet/poikien-nimet/${data.nimitiedot[2]?.Nimi}`, '_blank')}}
                            >
                            {data.nimitiedot[2]?.Nimi}
                            </Link>
                            <br/>
                          Lukumäärä ensimmäisenä nimenä: {data.nimitiedot[2]?.Lukumaara}
                          <br/>
                          {(data.nimitiedot[2]?.Lukumaara / Number(data.nimitiedot[0]?.Etunimet) * 100).toFixed(3) + "%"} kaikista ensimmäisistä nimistä
                          <br/>
                          {data.nimitiedot[2]?.id}. suosituin etunimi
                      </React.Fragment>
                      :<React.Fragment>
                            <Link
                            component="button"
                            onClick={() => {window.open(`https://lapsennimi.com/nimet/tyttojen-nimet/${data.nimitiedot[2]?.Nimi}`, '_blank')}}
                            >
                            {data.nimitiedot[2]?.Nimi}
                            </Link>
                            <br/>
                      Lukumäärä ensimmäisenä nimenä: {data.nimitiedot[2]?.Lukumaara}
                      <br/>
                      {(data.nimitiedot[2]?.Lukumaara / Number(data.nimitiedot[0]?.Etunimet) * 100).toFixed(3) + "%"} kaikista ensimmäisistä nimistä
                      <br/>
                      {data.nimitiedot[2]?.id}. suosituin ensimmäinen etunimi
                  </React.Fragment>
                      }
                      />
                  </ListItem>
                  {(formRef.current?.sukupuoli.value === "miehet")
                  ? data.nimitiedot?.map((nimi : any, idx : number) => {
                      if (idx > 2){
                        return <ListItem key={idx}>
                        <ListItemText
                        secondary={<React.Fragment>
                            <Link
                            component="button"
                            onClick={() => {window.open(`https://lapsennimi.com/nimet/poikien-nimet/${nimi?.Nimi}`, '_blank')}}
                            >
                            {nimi?.Nimi}
                            </Link>
                            <br/>
                            Lukumäärä: {nimi?.Lukumaara}
                            <br/>
                            {(nimi?.Lukumaara / Number(data.nimitiedot[1]?.MuutNimet) * 100).toFixed(3) + "%"} kaikista nimistä
                            <br/>
                            {nimi?.id}. suosituin nimi
                            <br/>
                        </React.Fragment>}
                        />
                    </ListItem>
                      }
                  })
                : data.nimitiedot?.map((nimi : any, idx : number) => {
                    if (idx > 2){
                      return <ListItem key={idx}>
                      <ListItemText
                      secondary={<React.Fragment>
                          <Link
                          component="button"
                          onClick={() => {window.open(`https://lapsennimi.com/nimet/tyttojen-nimet/${nimi?.Nimi}`, '_blank')}}
                          >
                          {nimi?.Nimi}
                          </Link>
                          <br/>
                          Lukumäärä: {nimi?.Lukumaara}
                          <br/>
                          {(nimi?.Lukumaara / Number(data.nimitiedot[1]?.MuutNimet) * 100).toFixed(3) + "%"} kaikista nimistä
                          <br/>
                          {nimi?.id}. suosituin nimi
                          <br/>
                      </React.Fragment>}
                      />
                  </ListItem>
                    }
                })
                }
                  <Divider/>
                  {data.sukunimi?.map((sukunimi, idx : number) => {
                    if (idx > 0)
                    {
                      return <ListItem key={idx}>
                      <ListItemText
                      secondary={<React.Fragment>
                        {sukunimi?.Sukunimi}
                        <br/>
                        Lukumäärä: {sukunimi?.Lukumaara}
                        <br/>
                        {(sukunimi?.Lukumaara / Number(data.sukunimi[0]?.lkm) * 100).toFixed(3) + "%"} kaikista sukunimistä
                      </React.Fragment>}
                      />
                    </ListItem>
                    }
                  })

                  }
              </List>
              </>
              :<></>
              }
          </Container>
</>
  )
}

export default Nimitietoa;
