import { Button, Container, Divider, FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Slider, Typography } from '@mui/material'
import React, { useRef, useState } from 'react'

interface Nimi{
    id : number;
    Nimi : string;
    Lukumaara : number;
  }

interface Data {
    nimitiedot : Nimi[],
    virhe : string,
    dataHaettu : boolean
  }

const Nimiarpoja : React.FC<any> = (props : any) : React.ReactElement => {

    const formRef : any = useRef<HTMLFormElement>()
    const [slider, setSlider] = useState<number>();

    const [data, setData] = useState<Data>({
        nimitiedot : [],
        virhe : "",
        dataHaettu : false
      });

      const sliderPituus : any = (pituus : number) => {
        setSlider(pituus);
      };


      const kaynnistaArvonta = async (e : React.FormEvent) : Promise<void> => {

          e.preventDefault();

          try {
            const yhteys = await fetch(`/api/nimiarvonta?minimi=${formRef.current.Lukumaara[0].value}&maksimi=${formRef.current.Lukumaara[1].value}&nimienlkm=${formRef.current.nimikpl.value}&sukupuoli=${formRef.current.sukupuoli.value}`)
            
            setData({
                ...data,
                nimitiedot : await yhteys.json(),
                dataHaettu : true,
            });
              
          } catch (error) {
              console.log(error);
          }
      };
      
  return (
<>
        <Typography variant="h3" sx={{textAlign:"center", fontWeight:"bold"}}>Nimiarpoja</Typography>

        <Container
        ref={formRef}
        component="form"
        onSubmit={kaynnistaArvonta}
        sx={{marginTop:4, marginBottom:4}}
        >
          <FormControl fullWidth>
          <FormLabel>Sukupuoli</FormLabel>
            <RadioGroup
              defaultValue="mies"
              row
              name="sukupuoli"
            >
              <FormControlLabel value="mies" control={<Radio />} label="Mies" />
              <FormControlLabel value="nainen" control={<Radio />} label="Nainen" />
            </RadioGroup>
            
            <FormLabel sx={{marginTop:3}}>Nimien määrä</FormLabel>
            <RadioGroup
              defaultValue="1"
              row
              name="nimikpl"
            >
              <FormControlLabel value="1" control={<Radio />} label="Yksi nimi" />
              <FormControlLabel value="2" control={<Radio />} label="Kaksi nimeä" />
              <FormControlLabel value="3" control={<Radio />} label="Kolme nimeä" />
            </RadioGroup>

            <FormLabel sx={{marginTop:3}}>Nimien harvinaisuus (omistajien lukumäärä)</FormLabel>
              <Slider
              sx={{width:"50%", marginBottom:5}}
              name="Lukumaara"
              valueLabelDisplay='auto'
              min={0}
              max={300000}
              defaultValue={[0, 300000]}
              step={10000}
              />

        <Button
        type="submit"
        variant="contained"
        sx={{width:"50%", margin:"auto"}}
          >
              Arvo nimet
          </Button>
          </FormControl>
        </Container>

          <Container sx={{textAlign:"center"}}>
              {(data.nimitiedot)
              ?<>
              <Divider/>
              <Typography variant="h4" sx={{marginTop:2}}>{data.nimitiedot[0]?.Nimi} {data.nimitiedot[1]?.Nimi} {data.nimitiedot[2]?.Nimi}</Typography>
              </>
              :<></>
              }
          </Container>
</>
  )
}

export default Nimiarpoja;
